.page {
  color: #060b35;
  background-color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.page__header {
  margin-bottom: 50px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo {
  object-fit: cover;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  cursor: pointer;
  padding-block: 21px;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link:hover, .nav__link--is-active {
  color: #00acdc;
}

.nav__link--is-active:before {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.products {
  --columns-count: 4;
  grid-template-columns: repeat(var(--columns-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 0 40px 72px;
  display: grid;
}

@media (width >= 300px) {
  .products {
    --columns-count: 1;
  }
}

@media (width >= 488px) {
  .products {
    --columns-count: 2;
  }
}

@media (width >= 768px) {
  .products {
    --columns-count: 3;
  }
}

@media (width >= 1024px) {
  .products {
    --columns-count: 4;
  }
}

.products__card {
  width: 200px;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__preview {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__model {
  width: 100%;
  height: 100%;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-content {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
}
